@charset "UTF-8";
@import url("//fonts.googleapis.com/earlyaccess/notosanstc.css");
*, *::before, *::after { box-sizing: border-box; }

body { background: #f5fbff; letter-spacing: 1.5px; font-family: 'Noto Sans TC', "微軟正黑體", "Microsoft JhengHei", "MingLiU", "sans-serif", serif; }

img { display: block; max-width: 100%; height: auto; }

.navbar-wrap { position: fixed; top: 0; left: 0; bottom: 0; z-index: 998; width: 250px; height: 100%; overflow-y: auto; background: linear-gradient(to top, #1e3c72 0%, #2a5298 100%); color: rgba(255, 255, 255, 0.87); }

.navbar-wrap .burger-button { display: none; position: fixed; right: 260px; }

.navbar-wrap .burger-button span { display: block; margin: 0.375rem 0; width: 2.625rem; height: 3px; background: #2a5298; }

.navbar-wrap .avatar { padding: 1rem; }

.navbar-wrap .avatar img { margin: 0 auto; max-width: 120px; border: 1px solid #1e3c72; border-radius: 50%; }

.navbar-wrap .avatar figcaption { line-height: 2; text-align: center; font-size: 2rem; font-weight: 200; }

.navbar-wrap .analytics { display: flex; justify-content: space-around; align-items: center; padding: 1rem 0; font-weight: 200; }

.navbar-wrap .analytics .views, .navbar-wrap .analytics .email { display: flex; flex-direction: column; align-items: center; }

.navbar-wrap .analytics a { color: inherit; }

.navbar-wrap .analytics .svg-inline--fa { font-size: 2.5rem; }

.navbar-wrap .navbar { padding: 1rem 0; }

.navbar-wrap .navbar a { display: block; padding: 1rem; overflow: hidden; text-transform: capitalize; text-decoration: none; color: rgba(255, 255, 255, 0.87); transition: all 0.5s cubic-bezier(0.3, 0.1, 0.05, 1); }

.navbar-wrap .navbar a:hover { background: rgba(0, 0, 0, 0.2); color: #FFF; }

.navbar-wrap .navbar .svg-inline--fa { margin-right: 1rem; font-size: 1.25rem; }

.navbar-wrap .navbar .divider { padding: 1rem; font-size: 0.75rem; font-weight: 200; color: rgba(255, 255, 255, 0.54); }

.navbar-wrap .navbar .divider ~ .divider { margin-top: 2rem; }

@media screen and (max-width: 768px) { .navbar-wrap { left: initial; right: 0; }
  .navbar-wrap .burger-button { display: block; } }

.contents-wrap { position: absolute 0; z-index: 1; margin-left: 250px; padding: 1rem; }

@media screen and (max-width: 768px) { .contents-wrap { margin-left: 0; } }
